import "swiper/css";
import styles from './SwiperScreen.css';
import {Swiper, SwiperSlide} from "swiper/react";
import {useEffect, useState} from "react";
import {useSelector} from "react-redux";
import {Screen1} from "../screens/Screen1/Screen1";
import {Screen2} from "../screens/Screen2/Screen2";
import {Screen3} from "../screens/Screen3/Screen3";
import {Screen4} from "../screens/Screen4/Screen4";

export const SwiperScreen = () => {

  const [swiperRef, setSwiperRef] = useState(null);
  const activeSlide = useSelector(state => state.swiper.activeSlide)

  useEffect(() => {
    swiperRef !== null && swiperRef.slideTo(activeSlide, 300);
  }, [activeSlide]);

  const SCREENS = [Screen1, Screen2, Screen3, Screen4];

  return (
    <Swiper
      onSwiper={setSwiperRef}
      initialSlide={activeSlide}
      resistanceRatio={0}
      className={styles.swiper}
      allowTouchMove={false}
    >
      {SCREENS.map(Component => (
        <SwiperSlide>
          <Component />
        </SwiperSlide>
      ))}
    </Swiper>
  );
};