import {createSlice} from "@reduxjs/toolkit";
import {countTotalPrice} from "../count/countTotalPrice";

const initialState = {
  selectedServices: {
    data_screen_1: {
      roomType: 'Home',
      cleaningType: '',
      cleaningOffice: '',
      roomNumber: '',
      square: '',
    },
    data_screen_2: {
      day: '',
      time: ''
    },
    data_screen_3: {

    },
  },
  totalPrice: 0,
}

const Data = createSlice({
  name: 'Data',
  initialState,
  reducers: {
    setDataScreen_1(state, action) {
      state.selectedServices.data_screen_1 = {
        ...state.selectedServices.data_screen_1,
        ...action.payload,
      };
    },
    setDataScreen_2(state, action) {
      state.selectedServices.data_screen_2 = {
        ...state.selectedServices.data_screen_2,
        ...action.payload,
      };
    },
    setDataScreen_3(state, action) {
      state.selectedServices.data_screen_3 = {
        ...state.selectedServices.data_screen_3,
        ...action.payload,
      };
    },
    setTotalPrice(state, action) {
      state.totalPrice = countTotalPrice(state.selectedServices.data_screen_1);
    }
  }
});

export const {
  setDataScreen_1,
  setDataScreen_2,
  setDataScreen_3,
  setTotalPrice
} = Data.actions;

export default Data.reducer;