export const validateScreen_1 = (data) => {
  let flag = false
  if(data.roomType === 'Home') {
    if(data.cleaningType === '' || data.roomNumber === '') flag = true
  } else if(data.roomType === 'Office') {
    if(data.cleaningOffice === '' || data.square === '' || data.square === '0') flag = true
  }
  return flag
};