import {setActiveSlide} from "../../../store/reducers/Swiper";
import {useDispatch, useSelector} from "react-redux";
import styles from "../Screen2/Screen2.module.css";
import {ButtonFurther} from "../../../universal/ButtonFurther/ButtonFurther";
import stylesMain from './Screen4.module.css';
const tg = window.Telegram.WebApp;

export const Screen4 = () => {

  const {
    data_screen_1,
    data_screen_2,
    data_screen_3,
  } = useSelector(state => state.data.selectedServices);

  const onSetDataBot = () => {
    tg.sendData(JSON.stringify({
      ...data_screen_3,
      ...data_screen_2,
      ...data_screen_1,
    }));
    setTimeout(() => tg.close(), 0);
  };

  const onSubmit = () => {
    onSetDataBot();
  };

  const dispatch = useDispatch();
  return (
    <div className={stylesMain.container}>
      <p className={stylesMain.name}>Confirm:</p>
      <div className={stylesMain.infoBlock}>
        <p><span>Room Type: </span>{`${data_screen_1.roomType}`}</p>
        {data_screen_1.roomType === 'Office' &&
          <p><span>Cleaning Type: </span>{`${data_screen_1.cleaningOffice}`}</p>
        }
        {data_screen_1.roomType === 'Home' &&
          <p><span>Cleaning Type: </span>{`${data_screen_1.cleaningType}`}</p>
        }
        {data_screen_1.roomType === 'Home' &&
          <p><span>Number of rooms: </span>{`${data_screen_1.roomNumber}`}</p>
        }
        {data_screen_1.roomType === 'Office' &&
          <p><span>Square: </span>{`${data_screen_1.square}`} m²</p>
        }
        <br/>
        <p><span>Name: </span>{`${data_screen_3.name} ${data_screen_3.lastName === '' ? '' : data_screen_3.lastName}`}</p>
        <p><span>Address: </span>{`${data_screen_3.city}, ${data_screen_3.address}`}</p>
        <p><span>Data: </span>{`${data_screen_2.day} ${data_screen_2.time} o'clock`}</p>
        {data_screen_3.email !== '' && <p><span>Email: </span>{`${data_screen_3.email}`}</p>}
        <p><span>Phone Number: </span>{`+971 ${data_screen_3.phone}`}</p>
      </div>
      <br/>
      <div className={styles.bottomBtns}>
        <ButtonFurther
          title={'Back'}
          callback={() => dispatch(setActiveSlide(2))}
          className={styles.continue}
        />
        <ButtonFurther
          disabled={false}
          title={'Order'}
          callback={() => onSubmit()}
          className={styles.continue}
        />
      </div>
    </div>
  )
}