import styles from './styles.module.css';

export const InputSquare = ({
                              square,
                              setSquare,
                              show,
                              name
                            }) => {

  const handleAreaChange = (event) => {
    const input = event.target.value;
    const formattedInput = input.replace(/[^\d.]/g, ''); // удаление всех символов кроме цифр и точки
    setSquare(formattedInput);
  };

  return show && (
    <div className={styles.container}>
      <p>{name}</p>
      <div className={styles.areaInputContainer}>
        <input
          type="text"
          id="area-input"
          value={square}
          className={styles.areaInput}
          onChange={handleAreaChange}
          placeholder="0"
        />
        <div>m²</div>
      </div>
    </div>
  )
}