import styles from './ButtonFurther.module.css';

export const ButtonFurther = ({
  title = 'далее',
  callback ,
  className = null,
  disabled = false,
                                type = 'button'}) => {
  return (
    <button
      className={`${className} ${styles.btn} ${disabled && styles.disabled}`}
      onClick={callback}
      disabled={disabled}
      type={type}
    >{title}</button>
  )
}