import {createSlice} from "@reduxjs/toolkit";

const initialState = {
  activeSlide: 0,
}

const Swiper = createSlice({
  name: 'Swiper',
  initialState,
  reducers: {
    setActiveSlide (state, action) {
      state.activeSlide = action.payload;
    },
  }
});

export const {
  setActiveSlide
} = Swiper.actions;

export default Swiper.reducer;