import {Button} from "../../../universal/Button/Button";
import styles_global from '../Screen1/Screen1.module.css';
import styles from './styles.module.css';

export const ButtonsBlock = ({
                               name,
                               buttons,
                               active_btn,
                               set_active_btn,
                               className,
                               show
}) => {

  return show && (
    <div className={styles.container}>
      <p>{name}</p>
      <div className={className}>
      {buttons.map((item, index) => {
          return (
            <Button
              widthOutline={'3px'}
              className={styles_global.button}
              show={true}
              title={item}
              active={active_btn === item}
              callBack={set_active_btn}
            />
          )
      })}
      </div>
    </div>
  );
};