import styles from './Header.module.css';
import logo_svg from "../../image/logo_icon.svg";
import {useSelector} from "react-redux";
import {useEffect, useState} from "react";

export const Header = ({language}) => {

  const totalPrice = useSelector(state => state.data.totalPrice);
  const [basketActive, setBasketActive] = useState(false);

  useEffect(() => {
    setBasketActive(true);
    setTimeout(() => setBasketActive(false), 700)
  }, [totalPrice]);

  return (
    <div className={`${styles.container}`}>
      <div className={`${styles.wrapperPrice}`}>
        <p>{language === 'en' ? 'Total:' : 'Итого:'}</p>
        <span className={`${basketActive && styles.basketActiveOn}`}>{totalPrice}</span>
        <p>AED</p>
      </div>
      <div className={styles.logoBlock}>
        <h1>CRYSTAL<br/>CLEANING</h1>
        <img src={logo_svg} alt="logo_svg"/>
      </div>
    </div>
  );
};