import {validateEmail} from "./validateEmail";
import {validateName} from "./validateName";
import {validatePasswordTwo} from "./validatePasswordTwo";
import {validatePhone} from "./validatePhone";

export const validate = (values) => {
  let errors = {};
  validateName(values.name, errors);
  validatePhone(values.phone, errors);
  validateAddress(values.address, errors);
  validateCity(values.city, errors);
  return errors;
};

export const validateCity = (city, errors) => {
  if(!city) errors.city = 'Enter a last City';
}
export const validateAddress = (address, errors) => {
  if(!address) errors.address = 'Enter a last address';
}
export const validateLastName = (name, errors) => {
  if(!name) errors.lastName = 'Enter a last Name';
}