import {Wrapper} from './ButtonStyled';
import styles from './Button.module.css';
import React, {useEffect, useState} from "react";

export const Button = ({
                         title = 'button',
                         theme = 'CRYSTAL',
                         show = false,
                         showDuration = 0.6,
                         className,
                         showStyles,
                         callBack,
                         active = false,
                         widthOutline ='5px'
                       }) => {

  const initialPositionShow = {x: 0, y: 0};
  const SHOW_BLOCK = React.createRef();
  const [positionShow, setPositionShow] = useState(initialPositionShow);
  const [classShow, setClassShow] = useState(false);

  const onShow = (e) => {
    const targetCoords = e.currentTarget.getBoundingClientRect();
    const x = e.clientX - targetCoords.left;
    const y = e.clientY - targetCoords.top;
    setPositionShow({x, y});
    setClassShow(false);
    setTimeout(() => setClassShow(true), 0);
  };

  const eventClick = (event) => {
    callBack && callBack(title);
    show && onShow(event);
  };

  const styledShowBlock = {
    borderRadius: '50%',
    animationDuration: showDuration + 's',
    opacity: 0,
    top: positionShow.y,
    left: positionShow.x,
    ...showStyles
  };

  const [opacity, setOpacity] = useState(0);

  const stylesOutlineShow = {
    strokeDasharray: '1000%',
    strokeWidth: widthOutline,
    fill: 'transparent',
    stroke: '#d0b386',
    transitionTimingFunction: 'linear',
    transition: '0.3s'
  };

  useEffect(() => {
    setTimeout(() => setOpacity(!active ? 0 : 1), 50)

  }, [active])

  return (
    <div className={styles.container}>
      <div className={styles.stylesOutline}>
        <svg width='100%' height="100%">
            <rect
                x="0"
                y="0"
                width="100%"
                height="100%"
                rx="19"
                stroke="red"
                strokeWidth="2"
                fill="none"
                opacity={0}
                style={{...stylesOutlineShow, opacity: opacity}}
            />
        </svg>
      </div>
      <Wrapper
        theme={theme}
        onClick={eventClick}
        className={className}
      >
        <div
          ref={SHOW_BLOCK}
          style={styledShowBlock}
          className={`${styles.showBlock} ${classShow && styles.showBlockOn}`}
        ></div>
        {title}
      </Wrapper>
    </div>
  );
};
