export const dataTime = (data) => {
  const mont = ['January' , 'February' , 'March' , 'April' , 'May' , 'June' , 'July' , 'August' , 'September' , 'October' , 'November' , 'December ']

  return data && `${data.getDate()} ${mont[data.getMonth()]}`;
}
export const dataStart = () => {
  const time = new Date();
  const mont = ['January' , 'February' , 'March' , 'April' , 'May' , 'June' , 'July' , 'August' , 'September' , 'October' , 'November' , 'December ']

  return `${time.getDate()} ${mont[time.getMonth()]}`;

}