import {useDispatch} from "react-redux";
import {validate} from "../../Auth/AuthForms/Validates/validate";
import {InputText} from "../../Auth/AuthForms/Inputs/InputText/InputText";
import {InputPassword} from "../../Auth/AuthForms/Inputs/InputPassword/InputPassword";
import {Form, Formik} from "formik";
import React from "react";
import stylesScreen3 from './Screen3.module.css';
import {InputNumber} from "../../Auth/AuthForms/Inputs/InputMask/InputMask";
import {ButtonFurther} from "../../../universal/ButtonFurther/ButtonFurther";
import {setActiveSlide} from "../../../store/reducers/Swiper";
import stylesSubmit from './../Screen2/Screen2.module.css';
import {setDataScreen_3} from "../../../store/reducers/Data";
const tg = window.Telegram.WebApp;

export const Screen3 = () => {

  const dispatch = useDispatch();

  const onSubmit = (values) => {
    dispatch(setDataScreen_3(values));
    dispatch(setActiveSlide(3));
  };

  return (
    <Formik
      initialValues={{
        name: tg.initDataUnsafe?.user?.username,
        lastName: '',
        email: '',
        phone: '',
        address: '',
        city: 'Dubai',
      }}
      onSubmit={onSubmit}
      validate={validate}
    >
      {({
          errors,
          touched,
          setFieldValue,
          handleSubmit,
        ...props
        }) => {

        return (
          <Form className={stylesScreen3.container} onSubmit={handleSubmit}>
            <div className={stylesScreen3.wrapper}>
              <p>Data people:</p>
              <InputText
                errors={errors.name}
                touched={touched.name}
                name='name'
                type='text'
                placeholder='Name'
              />
              <InputPassword
                errors={errors.lastName}
                touched={touched.lastName}
                name='lastName'
                type='text'
                placeholder='Last Name'
              />
              <InputText
                errors={errors.email}
                touched={touched.email}
                name='email'
                type='email'
                placeholder='Email'
              />
              <InputNumber
                errors={errors.phone}
                touched={touched.phone}
                mask="(99) 999-99-99"
                name="phone"
                placeholder='(__) ___-__-__'
                setFieldValue={setFieldValue}
              />
              <p>Address:</p>
              <InputText
                errors={errors.city}
                touched={touched.city}
                name='city'
                type='text'
                placeholder='City'
              />
              <InputText
                errors={errors.address}
                touched={touched.address}
                name='address'
                type='text'
                placeholder='Address'
              />
            </div>
            <div className={stylesSubmit.bottomBtns}>
              <ButtonFurther
                title={'Back'}
                callback={() => dispatch(setActiveSlide(1))}
                className={stylesSubmit.continue}
              />
              <ButtonFurther
                title={'Continue ➤'}
                type="submit"
                className={stylesSubmit.continue}
              />
            </div>
          </Form>
        )
      }}
    </Formik>
  );
};