const LIGHT = 500;
const PREMIUM = 1100;
const PREMIUM_ULTRA = 1500;
const HOURLY = 250;

const SUPPORTIVE = 5;
const GENERAL = 10;

export const countTotalPrice = (data) => {
  let price = 0;

  if(data.roomType === "Home") {
    switch (data.cleaningType) {
      case 'Light':
        price = data.roomNumber * LIGHT;
        break;
      case 'Premium':
        price = data.roomNumber * PREMIUM;
        break;
      case 'Premium Ultra':
        price = data.roomNumber * PREMIUM_ULTRA;
        break;
      case 'Hourly':
        price = data.roomNumber * HOURLY;
        break;
    }
  }
  if(data.roomType === "Office") {
    switch (data.cleaningOffice) {
      case 'Supportive':
        price = Number(data.square) * SUPPORTIVE;
        break;
      case 'General':
        price = Number(data.square) * GENERAL;
        break;
    }
  }


  return price;
}