import styled from "styled-components";

export const Wrapper = styled.button`
  position: relative;
  overflow: hidden;
  cursor: pointer;
  border: none;
  border-radius: 15px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  ${({theme}) => {
  switch (theme) {
    case 'CRYSTAL':
      return `
        background: #d0b386;
      `;
    default: return null;
  }
}}
`;

