import styles from './TimeDrop.module.css';
import {SwiperBlock} from "./TimeItem/SwiperBlock/SwiperBlock";
import {useEffect, useState} from "react";

export const TimeDrop = ({callBack}) => {

  const [hours, setHours] = useState('00')
  const [minutes, setMinutes] = useState('00')

  useEffect(() => {
    callBack(`${hours}:${minutes}`)
  }, [hours, minutes]);

  return (
    <div className={styles.container}>
      <SwiperBlock
        count={24}
        callBackActiveContent={setHours}
      />
      <p style={{color: '#d0b386'}}>:</p>
      <SwiperBlock
        count={60}
        step={5}
        callBackActiveContent={setMinutes}
      />
    </div>
  );
};


