import {Swiper, SwiperSlide} from "swiper/react";
import {TimeItem} from "../TimeItem";
import styles from './SwiperBlock.module.css';

export const SwiperBlock = ({count, step = 1, callBackActiveContent}) => {

  const handleSlideChange = (swiper) => {
    const activeSlideIndex = swiper.activeIndex;
    const activeSlide = swiper.slides[activeSlideIndex];
    const activeSlideChildren = activeSlide.children[0].innerHTML;
    callBackActiveContent(activeSlideChildren);
  };

  return (
    <div className={styles.container}>
      <Swiper
        freeModeMomentum={true}
        className={styles.wrapper}
        freeMode={true}
        loop={true}
        initialSlide={0}
        direction={"vertical"}
        slidesPerView={5}
        centeredSlides={true}
        onSlideChange={handleSlideChange}
      >
        {Array.from({ length: count }, (_, index) => {
          if ((index) % step === 0) {
            return (
              <SwiperSlide
                key={index}
                style={{
                  overflow: 'hidden',
                }}
              >
                <TimeItem
                  name={index.toString().length === 1 ? '0' + index : index}
                />
              </SwiperSlide>
            );
          }
          return null;
        })}
      </Swiper>
      <div className={styles.outlineBlock}></div>
      <div className={`${styles.shadow} ${styles.shadowTop}`}></div>
      <div className={`${styles.shadow} ${styles.shadowBottom}`}></div>
    </div>
  );
};